.table {
  width: 100%;
  min-width: 800px;
  vertical-align: top;
  border-color: rgb(224, 224, 224);
  overflow: hidden;
  border-radius: 7px;
}
.table-scroll {
  position: relative;
  margin: auto;
  overflow: hidden;
  background-color: rgb(255, 255, 255);
}
@media screen and (max-width: 832px) {
  .table-scroll {
    max-width: 800px;
  }
}
.table-wrap {
  width: 100%;
  overflow: auto;
  padding: 0px 16px;
}
.table-wrap::-webkit-scrollbar {
  height: 5px;
}
.table-wrap::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgb(12, 180, 241);
}
.table-wrap::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background-color: #f9f9fd;
}
.table__close {
  cursor: pointer;
}
.table__close:hover {
  opacity: 0.6;
}
.table > thead {
  vertical-align: bottom;
}
.table > tbody > tr {
  vertical-align: inherit;
  overflow-x: auto;
  overflow-y: hidden;
  border-top-width: 1px;
  border-top-color: rgb(224, 224, 224);
}
.table > :not(caption) > * > * {
  padding: 16px 0px;
}

