.search {
  background: #FFFFFF;
  box-shadow: 0px 18px 15px rgba(148, 148, 148, 0.15);
  border-radius: 7px;
  padding: 12px 16px 16px 16px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.search__label {
  position: relative;
  display: block;
  padding: 0;
  height: 34px;
  max-width: 900px;
  background: #ECEFF0;
  border-radius: 4px;
  color: rgb(158, 170, 180);
  border: none;
  padding-left: 31px;
  margin-bottom: 24px;
}
.search__icon {
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto auto auto 0;
  left: 3px;
  width: 24px;
  height: 24px;
}
.search__input {
  height: 34px;
  width: 100%;
  background: #ECEFF0;
  border-radius: 4px;
  color: rgb(51, 51, 51);
  font-family: "Inter Bold", sans-serif;
  border: none;
}
.search__input::placeholder {
  color: rgb(158, 170, 180);
}
.search__input:focus-visible {
  outline: none;
}
.search__reset-button {
  width: 130px;
  display: flex;
  align-items: center;
  cursor: pointer;
  display: none;
}
.search__reset-button_active {
  display: flex;
}
.search__reset-button:hover {
  opacity: 0.6;
}

.sortable {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 211px;
  color: rgb(158, 170, 180);
  font-size: 10px !important;
  line-height: 14px !important;
  margin-bottom: 15px;
}
.sortable__button {
  color: rgb(158, 170, 180);
  border-bottom: 1px dashed rgb(158, 170, 180);
  cursor: pointer;
}
.sortable__button_active {
  color: rgb(51, 51, 51);
  border-bottom: 1px dashed rgb(51, 51, 51);
}
.sortable__button:hover {
  opacity: 0.6;
}

.pagination {
  display: flex;
  justify-content: center;
}
.pagination__item {
  width: 28px;
  height: 28px;
  border-radius: 3px;
  background: rgb(12, 180, 241);
  margin-right: 4px;
  cursor: pointer;
}
.pagination__item:hover {
  opacity: 0.6;
}
.pagination__item:last-child {
  margin-right: 0;
}
.pagination__link {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: rgb(255, 255, 255);
  font-family: "Inter Medium", sans-serif;
  line-height: 28px;
  cursor: pointer;
}
.pagination__link:hover {
  opacity: 0.6;
}
.pagination__link-arrow {
  font-family: "Inter Bold", sans-serif;
}

.modal {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  background-color: rgb(79, 79, 79);
  justify-content: center;
  align-items: center;
  display: none;
}
.modal_active {
  display: flex;
}
.modal__card-title {
  padding-bottom: 24px;
}
.modal__card {
  max-width: 353;
  border-radius: 8px;
  background: rgb(255, 255, 255);
  padding: 36px 36px 32px 36px;
}
.modal__card-buttons {
  display: flex;
  justify-content: center;
}